.flip-card {
  background-color: transparent;
  perspective: 1000px;
  width: 290px;
  height: 400px;
  position: relative;
  flex: content;
  cursor: pointer;
}

@media (min-width: 425px){
  .flip-card {
    height: 475px;
    width: 100%;
  }
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

@media (min-width: 1024px) {
  .flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
  }

  .flip-card{
    cursor: -webkit-grab ;
  }
}

@media (max-width: 1023px){
  .flip-card.open .flip-card-inner {
  transform: rotateY(180deg);
}}

.flip-card-front, .flip-card-back {
  position: absolute; 
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  border-radius: 10px;
}

.flip-card-front {
  background-color: #D8D8D8;
  color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flip-card-back {
  background-color: #333;
  color: white;
  transform: rotateY(180deg);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.thumbnail-container {
  position: relative; /* This will contain the gradient overlay within the card */
  width: 100%;
  height: 100%;
  max-height: 222px;
  border-radius: 10px 10px 0 0;
  overflow: hidden; /* Ensure content doesn't overflow out of the container */
}

.thumbnail-overlay {
  object-position: top;
  object-fit: cover;
  width: 100%;
  height: 100%;
  border-radius: inherit;
  filter: blur(1px);
  -webkit-filter: blur(1px);
} 

.thumbnail-container::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to top, rgba(216, 216, 216, 1), rgba(216, 216, 216, 0));
  border-radius: inherit;
  z-index: 1;
  pointer-events: none;
}

.status-done {
  background-color: green;
  padding: 5px 10px;
  color: white;
  border-radius: 5px;
}

.status-ongoing {
  background-color: yellow;
  padding: 5px 10px;
  color: black;
  border-radius: 5px;
}

.skill-grid-back {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5px;
  max-height: 115px;
  overflow: auto;
}

.skill-grid {
  gap: 5px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
}

.skill-tag {
  font-size: 9px;
  overflow: hidden;
  text-overflow: ellipsis;
  border-radius: 5px;
  white-space: nowrap;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  height: 25px;
}

.button-hover-effect {
  transition: transform 0.3s ease-in-out;
}

.button-hover-effect:hover {
  transform: scale(1.04);
}

.button-hover-effect:active {
  background-color: #F5F5F5;
  color: #181B19;
  transform: scale(1);
  transition: transform 0.6s ease-in-out;
}

.filter-buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.filter-button {
  background-color: #090C0A;
  color: #D8D8D8;
  border: none;
  padding: 10px 20px;
  margin: 0.5rem;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease, transform 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
}

.filter-button:hover {
  transform: scale(1.04);
}

.filter-button:active {
  background-color: #D96C06;
  color: #D8D8D8;
  transform: scale(1);
  transition: transform 0.6s ease-in-out;
}

.filter-button input[type="checkbox"] {
  display: none;
}

.filter-button.active {
  background-color: #BABBBB;
  color: #090C0A;
}

.filter-button.active input[type="checkbox"] {
  background-color: #181B19;
}

.py-20 {
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.mb-8 {
  margin-bottom: 2rem;
}

.mt-16 {
  margin-top: 4rem;
}

.project-card {
  background-color: #D8D8D8;
  color: #181B19;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  height: 100%;
}

.project-card h3 {
  font-size: x-large;
}

.project-card p {
  font-size: smaller;
  padding-top: 20px;
  padding-bottom: 20px;
}

.project-card a,
.project-card button {
  margin-top: auto;
  text-align: center;
  padding: 10px 20px;
}

.status-button {
  background-color: #f0f0f0;
  color: #181B19;
  border: 1px solid #181B19;
  display: inline-block;
  padding: 5px 10px;
  margin-top: 5px;
  text-align: center;
  border-radius: 5px;
  margin-bottom: -10px;
}

.status-done {
  background-color: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}

.status-ongoing {
  background-color: #fff3cd;
  color: #856404;
  border: 1px solid #ffeeba;
}

.pro-desc {
  background-color: #f0f0f0;
  color: #090C0A;
  overflow: auto;
  margin-top: 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: left;
}

.skills-section {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 10px;
}

.skill-tag {
  padding: 6px 10px;
  margin: 5px;
  border-radius: 5px;
  color: #fff;
  font-weight: 600;
}

/* Scrollbar Styling */
::-webkit-scrollbar{
  width: 5px;
}
::-webkit-scrollbar-track{
  background: #181B19;
}
::-webkit-scrollbar-thumb{
  background: linear-gradient(#BABBBB, #F5F5F5);
  border-radius: 10px;
  
}

/* Debugging backside */
.show-back .flip-card-back {
  transform: rotateY(0deg);
  z-index: 2;
}

.show-back .flip-card-front {
  transform: rotateY(-180deg);
  z-index: 1;
}

@media (max-width: 768px) {
  h3 {
    font-size: 0.97em;
  }

  p {
    font-size: 0.97em;
  }

  .project-card {
    padding: 0.97rem;
  }

  .project-card h3 {
    font-size: 0.97em;
  }

  .project-card p {
    font-size: 0.97em;
  }

  .project-card a,
  .project-card button {
    padding: 0.97em 1.94em;
  }
}

